@import "../../GlobalStyles/GlobalStyles.scss";
.search_wrap {
    &:focus-within {
        border: 1px solid $textClr;
    }
    .clear,
    .loading {
        color: $fadeClr;
        transition: 0.25s ease;
        display: none;
        &:hover {
            color: $mainClr;
        }
    }
    .loading {
        color: $mainClr;
    }
    min-width: 36rem;
    height: 2.8rem;
    border-radius: 92px;
    padding: 5px 10px;
    padding-right: 0px;
    overflow: hidden;
    border: 1px solid $fadeClr;
    transition: 0.25s ease;
    z-index: 99;
    @include flex(center);
    justify-content: space-between;
    @media screen and (max-width: 768px) {
        display: none;
        min-width: 0rem;
    }
    input {
        width: 26rem;
        height: 2.5rem;
        padding-left: 1rem;
        font-size: 1.4rem;
        color: $textClr;
        caret-color: $mainClr;
        @media screen and (max-width: 768px) {
            font-size: 1.2rem;
        }
        &::placeholder {
            color: $fadeClr;
        }
        &:not(:placeholder-shown) ~ .search_btn {
            color: $mainClr;
        }
        &:not(:placeholder-shown) ~ .clear {
            display: block;
        }
    }
    .search_btn {
        width: 6rem;
        height: 5rem;
        padding-right: 0.5rem;
        font-size: 1.9rem;
        transition: 0.25s ease;
        position: relative;
        color: $fadeClr;
        &:hover {
            color: $blue;
        }
        &:active > .search_btn-icon {
            transform: scale(1.03);
        }
        // &::before {
        //     position: absolute;
        //     content: "";
        //     width: 1px;
        //     height: 3rem;
        //     background-color: $fadeClr;
        //     top: 50%;
        //     left: 0;
        //     transform: translateY(-50%);
        // }
    }
}
.search_title {
    width: fit-content;
    background-color: $fadeClr500;
    font-size: 1.4rem;
    padding: 1px 15px;
    border-radius: 5px;
    @media screen and (max-width: 768px) {
        font-size: 1.2rem;
    }
}

@media screen and (max-width: 768px) {
    .mobile {
        position: absolute;
        width: 95%;
        background-color: $white;
        z-index: 9;
        height: 32px;
        top: 8px;
        left: 50%;
        transform: translateX(-50%);
        display: none;
        &:focus-within {
            border: 0.5px solid $textClr;
        }
    }
    .modal {
        position: fixed;
        width: 100vw;
        height: 0vh;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.6);
        z-index: -1;
        & > .modal_top {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 60px;
            background-color: white;
        }
    }
}

.find {
    cursor: pointer;
    color: $textClr;
    font-size: 2rem;
    margin-top: 7px;
    transition: 0.35s ease;
    text-align: center;
    font-weight: bold;
    display: none;
    @media screen and (max-width: 768px) {
        display: block;
        position: absolute;
        right: 67px;
        top: 10px;
    }
    &:hover {
        color: $blue;
    }
}
