@import "../GlobalStyles/GlobalStyles.scss";
.wrapper {
    width: 60px;
    height: 100%;
    padding: 15px;
    @media screen and (max-width: 768px) {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100vw;
        height: 55px;
        padding: 0;
        text-align: center;
        border-top: 2px $fadeClr500 solid;
        padding-top: 1px;
    }
    .inner {
        padding-top: 8rem;
        margin-left: 1rem;
        width: 100%;
        height: 500px;
        @include flex(center);
        flex-direction: column;
        justify-content: flex-start;
        gap: 10px;
        @media screen and (max-width: 768px) {
            position: absolute;
            width: 100%;
            height: 45px;
            flex-direction: row;
            justify-content: space-evenly;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            padding: 0;
            margin-left: 0rem;
            column-gap: 0px;
        }
        .link_item {
            @include flex(center);
            flex-direction: column;
            gap: 6px;
            width: max-content;
            font-size: 1.2rem;
            width: 70px;
            padding: 1.1rem 0;
            border-radius: 10px;
            transition: 0.2s ease;
            color: $textClr;
            @media screen and (max-width: 768px) {
                width: 60px;
                padding: 1.1rem 0;
                margin-bottom: 3px;
            }
            &:hover {
                background-color: $fadeClr500;
            }
            .icon {
                font-size: 1.8rem;
                transition: 0.2s ease;
                @media screen and (max-width: 768px) {
                    padding: 2px 0;
                    padding-bottom: 3px;
                }
            }
            span {
                @media screen and (max-width: 768px) {
                    display: none;
                }
            }
            &.active {
                background-color: $fadeClr500;
                @media screen and (max-width: 768px) {
                    background-color: $mainClr;
                }
                transition: 0.25s ease;
                .icon {
                    color: $textClr;
                    transform: scale(1.08);
                }
            }
        }
    }
}
