@import "./variable";
@import "./mixin";
html {
    font-size: 62.5%;
    scroll-behavior: smooth;
}
body {
    font-family: "Roboto", sans-serif;
    font-size: 1.6rem;
    line-height: 1.5;
    text-rendering: optimizeSpeed;
    width: 100vw;
    overflow-x: hidden;
    z-index: 0;
}
* {
    padding: 0;
    margin: 0;
    text-decoration: none;
    color: unset;
    border: none;
    outline: none;
    background-color: unset;
    -webkit-tap-highlight-color: transparent;
    &::selection {
        background-color: $blue;
        color: white;
    }
    &:hover {
    }
}
button {
    cursor: pointer;
}
