@import "../../GlobalStyles/GlobalStyles.scss";
.nav_wrap {
    width: 180px;
    height: 100%;
    transform: translateX(-5rem);
    @include flex(center);
    gap: 3rem;
    color: $darkClr;
    font-weight: 500;
    @media screen and (max-width: 768px) {
        width: 10px;
        margin-right: -5px;
        gap: 2rem;
        text-align: right;
        & .recent {
            display: none;
        }
    }
    span {
        cursor: pointer;
        color: $textClr;
        font-size: 1.9rem;
        margin-top: 3px;
        transition: 0.3s ease;
        text-align: center;
        font-weight: bold;
        &:hover {
            color: $blue;
        }
        &:active {
            transform: scale(1.03);
        }
    }
}
.search_title {
    width: 98px;
    @include flex(center);
    font-size: 1.3rem;
    justify-content: space-between;
    background-color: $fadeClr500;
    font-weight: 500;
    padding: 3px 11px;
    border-radius: 5px;
}
