@import "../GlobalStyles/GlobalStyles.scss";
.wrapper {
    display: flex;
    flex-direction: column;
    gap: 25px;
    background: $white;
    border-radius: 8px;
    box-shadow: 0 0 2px $textClr;
    padding: 20px 20px;
    color: $textClr;
    overflow-y: auto;
    overflow-x: hidden;
    max-height: 500px;
    min-height: 137px;
    @media screen and (max-width: 768px) {
        gap: 18px;
    }
}
::-webkit-scrollbar {
    width: 3px;
}
::-webkit-scrollbar-track {
    background: $graylightClr;
    margin-top: 4px;
}
::-webkit-scrollbar-thumb {
    background: $mainClr;
}
