@import "../GlobalStyles/GlobalStyles.scss";
.wrapper {
    width: 100%;
    height: 60px;
    box-shadow: 0 0 1px $darkClr;
    @media screen and (max-width: 768px) {
        height: 60px;
    }
    @include flex(center);
    .inner {
        height: 100%;
        width: 100vw;
        padding: 0 30px;
        @include flex(between);
        @media screen and (max-width: 768px) {
            padding: 0 5px;
            padding-right: 0px;
        }
    }
    .logo {
        height: 50px;
        width: 180px;
        cursor: pointer;
        transition: 0.35s ease;
        & img {
            width: 100%;
            height: 100%;
        }
        @media screen and (max-width: 768px) {
            height: 45px;
            width: 185.32px;
            margin-left: 5px;
        }
        &:active {
            transform: scale(1.03);
        }
    }
    .bar {
        cursor: pointer;
        color: $textClr;
        font-size: 2.3rem;
        margin-top: 3px;
        transition: 0.35s ease;
        text-align: center;
        font-weight: bold;
        display: none;
        @media screen and (max-width: 768px) {
            display: block;
            padding-right: 25px;
        }
        &:hover {
            color: $blue;
        }
        &:active {
        }
    }
}
