@import "../../GlobalStyles/GlobalStyles.scss";
.wrapper {
    color: $textClr;
    border-bottom: 1px dotted $fadeClr;
    padding-bottom: 1.5rem;
    width: 22rem;
    height: 6rem;
    &:hover .car_name {
        color: $blue;
    }
    &:active .car_name {
        transform: translateX(-3px);
    }
    &:hover .car_avatar {
        transform: scale(1.03);
    }
    &:active .car_avatar {
        transform: translateX(3px);
    }
    @include flex(center);
    justify-content: flex-start;
    cursor: pointer;
    gap: 20px;
    .car_avatar {
        height: 50px;
        object-fit: cover;
        transition: 0.35s ease;
    }
    .car_info {
        @include flex(center);
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        width: 15rem;
        height: 20px;
        gap: 6px;
        .car_name {
            @include flex(center);
            gap: 20px;
            transition: 0.35s ease;
            font-size: 1.3rem;
        }
    }
    .car_description {
        & .car_brand {
            font-weight: 500;
            font-size: 0.55rem;
            color: $mainClr;
            @include flex(center);
            gap: 4px;
            & span {
                font-size: 1.2rem;
                color: $textClr;
            }
        }
        display: flex;
        gap: 4px;
    }
}
