@import "../../GlobalStyles/GlobalStyles.scss";
.wrapper {
    color: $textClr;
    border-bottom: 1px dotted $fadeClr;
    padding-bottom: 1.5rem;
    &:hover .car_name {
        color: $blue;
    }
    &:active .car_name {
        transform: translateX(-3px);
    }
    &:hover .car_avatar {
    }
    &:active .car_avatar {
        // transform: translateX(3px);
    }
    @include flex(center);
    justify-content: flex-start;
    cursor: pointer;
    gap: 28px;
    font-size: 1.4rem;
    @media screen and (max-width: 768px) {
        min-width: 40rem;
    }
    .car_avatar {
        width: 115px;
        height: 57.5px;
        object-fit: cover;
        transition: 0.2s ease-in;
        @media screen and (max-width: 768px) {
            width: 120px;
            height: 60px;
        }
    }

    .car_info {
        @include flex(center);
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        min-width: 17rem;
        height: 55px;
        @media screen and (max-width: 768px) {
            min-width: 12rem;
            .car_name {
                @include flex(center);
                font-size: 1.4rem;
            }
        }
        .car_name {
            @include flex(center);
            gap: 20px;
            transition: 0.35s ease;
        }
    }
    .car_price {
        font-weight: bold;
        @include flex(center);
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        height: 55px;
        @media screen and (max-width: 768px) {
            font-size: 1.2rem;
            .car_price-vnd {
                font-size: 1.1rem;
            }
        }
        .car_price-vnd {
            font-weight: 600;
            font-size: 1.3rem;
        }
    }
    &:hover .car_version {
        color: $blue;
    }
    .car_description {
        & span {
            font-size: 1.2rem;
            font-weight: 600;
            background-color: $fadeClr500;
            padding: 1px 4px;
            border-radius: 3px;
            @media screen and (max-width: 768px) {
                font-size: 1.1rem;
                padding: 1px 3px;
            }
        }
        display: flex;
        gap: 7px;
    }
}
.no_result {
    width: 40rem;
    font-size: 1.3rem;
}
